import { useContext, useEffect, useState } from 'react';
import { AuthContext } from '../contexts/user/auth-state';
import { useNavigate } from 'react-router-dom';

const Settings = () => {
  const { user } = useContext(AuthContext);
  const navigate = useNavigate();
  const [config, setConfig] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    const getData = async () => {
      try {
        const res = await fetch('/config', {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${user?.token}`,
          },
        });

        if (!res.ok) {
          if (res.status === 401) {
            navigate('/', { replace: true });
          }
          throw new Error('Error');
        }
        const data = await res.json();
        console.log(data);
        setConfig(data.config);
      } catch (err) {
        console.log(err);
      }
      setLoading(false);
    };
    getData();
  }, []);

  if (!user) return null;

  if (loading)
    return (
      <div className="w-full h-full">
        <div className="top-0 left-0 w-full h-full flex justify-center items-center">
          <div className="animate-spin rounded-full h-28 w-28 border-t-[.25rem] border-b-[.25rem] border-[#5d712d]"></div>
        </div>
      </div>
    );

  return (
    <div className="flex">
      <div className="bg-slate-50/50 p-4 shadow">
        <div className="flex items-start">
          <button
            onClick={() => {
              navigate(-1);
            }}
            className="py-1 px-2 mr-4 flex items-center bg-slate-50/50 hover:bg-slate-50 hover:shadow-md text-slate-600 rounded-md shadow"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              className="w-5 h-5 stroke-slate-600"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M9 15L3 9m0 0l6-6M3 9h12a6 6 0 010 12h-3"
              />
            </svg>
            <p className="ml-1">Volver</p>
          </button>
          <h1 className="text-2xl font-semibold text-slate-700 mb-4">
            Configuracin
          </h1>
        </div>
        {config &&
          Object.keys(config).map((key: string, idx: number) => {
            return (
              <div
                key={key}
                className={`flex p-2 ${idx % 2 ? 'bg-slate-200' : ''}`}
              >
                <p className="w-[250px] flex-shrink-0">{key}</p>
                <p>{config[key]}</p>
              </div>
            );
          })}
      </div>
    </div>
  );
};

export default Settings;
