import { createContext, useEffect, useReducer } from 'react';
import { InitialState, User, AuthContextType, LoginResponse } from './types';
import authReducer from './auth-reducer';

const initialState: InitialState = {
  user: null,
  loading: true,
  initializing: true,
  errorMessage: '',
};

export const AuthContext = createContext<AuthContextType>(
  {} as AuthContextType,
);

const AuthState = ({ children }: { children: React.ReactNode }) => {
  const [state, dispatch] = useReducer(authReducer, initialState);

  useEffect(() => {
    initializing();
  }, []);

  const initializing = async () => {
    try {
      const token = localStorage.getItem('token');
      if (!token) throw new Error('No token');
      const res = await fetch('/api/v1/auth/verifyToken', {
        method: 'POST',
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      if (res.ok) {
        const data = await res.json();
        setUser(data.user);
      } else {
        throw new Error('user not verified');
      }
    } catch (err) {
      console.log(err);
      setUser(null);
    }
  };
  const setErrorMessage = async (message: string): Promise<void> => {
    dispatch({
      type: 'SET_ERROR_MESSAGE',
      payload: message,
    });
  };

  const login = async (credential: {
    email: string;
    password: string;
  }): Promise<LoginResponse> => {
    try {
      const res = await fetch('/api/v1/auth/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(credential),
      });

      if (res.ok) {
        const data = await res.json();
        localStorage.setItem('token', data.user.token);
        setUser(data.user);
        return { status: true, message: 'Login successful' };
      } else {
        const data = await res.json();

        return { status: false, message: data.message };
      }
    } catch (err) {
      console.log(err);
      const message = err instanceof Error ? err.message : 'Login failed';
      return { status: false, message };
    }
  };

  const signup = async (credential: {
    email: string;
    password: string;
    name: string;
    phone: string;
    code: string;
  }) => {
    try {
      const res = await fetch('/api/v1/auth/signup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(credential),
      });
      if (res.ok) {
        const data = await res.json();
        localStorage.setItem('token', data.user.token);
        setUser(data.user);
        return true;
      } else {
        throw new Error('Error');
      }
    } catch (err) {
      console.log(err);
      return false;
    }
  };

  const logout = async (): Promise<void> => {
    try {
      if (!state.user) return;
      localStorage.removeItem('token');
      setUser(null);
    } catch (err) {
      console.log(err);
    }
  };

  const AddProductToCart = async (
    product_id: string,
    variant: string,
  ): Promise<boolean> => {
    try {
      const token = localStorage.getItem('token');
      const res = await fetch(
        `/api/v1/cart/${state.user?.cart._id}/new/${product_id}`,
        {
          method: 'PUT',
          headers: {
            Authorization: `Bearer ${token}`,
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ variant }),
        },
      );
      if (res.ok) {
        const data = await res.json();
        if (state.user && data.cart) {
          setUser({ ...state.user, cart: data.cart });
        }
      } else {
        throw new Error('Error');
      }
      return true;
    } catch (err) {
      console.log(err);
      return false;
    }
  };
  const setUser = (user: User | null) => {
    dispatch({
      type: 'SET_USER',
      payload: user,
    });
  };

  const setCart = async (cart: any): Promise<void> => {
    dispatch({
      type: 'SET_CART',
      payload: cart,
    });
  };

  return (
    <AuthContext.Provider
      value={{
        user: state.user,
        loading: state.loading,
        initializing: state.initializing,
        errorMessage: state.errorMessage,
        login,
        signup,
        logout,
        AddProductToCart,
        setErrorMessage,
        setCart,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export default AuthState;
