import { motion, AnimatePresence } from 'framer-motion';
import logo from '../../assets/imgs/logo.png';
import { useContext, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { AuthContext } from '../../contexts/user/auth-state';

const styles = {
  empty:
    'border-transparent focus:border-[#92c07a] focus:shadow-md transition-shadow',
  valid: 'border-[#92c07a] shadow-md transition-shadow',
  invalid: 'border-red-500 focus:shadow-md transition-shadow',
};
const inputs = {
  email: {
    label: 'Correo electrnico',
    name: 'email',
    type: 'email',
    placeholder: 'Correo electrnico',
    errorMessages: [
      'Correo electrnico no vlido',
      'Correo electrnico ya registrado',
    ],
    helpMessage: null,
    validation: (value: string) => {
      const validEmail = /\S+@\S+\.\S+/.test(value);

      return validEmail;
    },
  },
  password: {
    label: 'Contrasea',
    name: 'password',
    type: 'password',
    placeholder: 'Contrasea',
    errorMessages: [],
    helpMessage: 'Mnimo 8 caracteres, una mayscula, un nmero y un smbolo',
    validation: (value: string) => {
      let validPassword = true;
      if (value.length < 8) {
        validPassword = false;
      }

      // Verificamos si la contrasea contiene al menos una letra minscula
      if (!/[a-z]/.test(value)) {
        validPassword = false;
      }

      // Verificamos si la contrasea contiene al menos una letra mayscula
      if (!/[A-Z]/.test(value)) {
        validPassword = false;
      }

      // Verificamos si la contrasea contiene al menos un nmero
      if (!/\d/.test(value)) {
        validPassword = false;
      }
      if (!/[^a-zA-Z0-9]/.test(value)) {
        validPassword = false;
      }

      return validPassword;
    },
  },
  name: {
    label: 'Nombre',
    name: 'name',
    type: 'text',
    placeholder: 'Nombre',
    errorMessages: ['Nombre no vlido'],
    helpMessage: null,
    validation: (value: string) => {
      const validName = /^[a-zA-Z-\s]{1,40}$/.test(value);
      return validName;
    },
  },
  phone: {
    label: 'Telfono',
    name: 'phone',
    type: 'tel',
    placeholder: '+525524938034',
    errorMessages: ['Telfono no vlido'],
    helpMessage: 'Recibira un mensaje con un cdigo de verificacin',
    validation: (value: string) => {
      const validPhone = /^[+][0-9]{10,}$/.test(value);
      return validPhone;
    },
  },
  code: {
    label: 'Cdigo de verificacin',
    name: 'code',
    type: 'text',
    placeholder: '012345',
    errorMessages: ['Cdigo no vlido'],
    helpMessage: null,
    validation: (value: string) => {
      const validCode = /^[0-9]{6}$/.test(value);
      return validCode;
    },
  },
};
const forms = [['email', 'password'], ['name', 'phone'], ['code']];

type Status = 'empty' | 'valid' | 'invalid';

type Data = {
  email: {
    value: string;
    indexError: number;
    status: Status;
  };
  password: {
    value: string;
    indexError: number;
    status: Status;
  };
  name: {
    value: string;
    indexError: number;
    status: Status;
  };
  phone: {
    value: string;
    indexError: number;
    status: Status;
  };
  code: {
    value: string;
    indexError: number;
    status: Status;
  };
};

const Signup = () => {
  const navigate = useNavigate();
  const { signup } = useContext(AuthContext);
  const [step, setStep] = useState(0);
  const [loading, setLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [data, setData] = useState<Data>({
    email: {
      value: '',
      indexError: 0,
      status: 'empty',
    },
    password: {
      value: '',
      indexError: 0,
      status: 'empty',
    },
    name: {
      value: '',
      indexError: 0,
      status: 'empty',
    },
    phone: {
      value: '',
      indexError: 0,
      status: 'empty',
    },
    code: {
      value: '',
      indexError: 0,
      status: 'empty',
    },
    // name: '',
    // phone: '',
    // code: '',
  });

  const handleClick = async () => {
    if (step === 0) {
      if (data.password.status !== 'valid' || data.email.status !== 'valid') {
        forms[0].forEach((input) => {
          if (data[input as keyof Data].status !== 'valid') {
            setData((prev) => ({
              ...prev,
              [input]: {
                ...prev[input as keyof Data],
                indexError: 0,
                status: 'invalid',
              },
            }));
          }
        });
        return;
      }

      const res = await fetch('/api/v1/auth/verify-email', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email: data.email.value }),
      });
      if (res.status === 200) {
        const data = await res.json();

        if (data.emailExists) {
          return setData((prev) => ({
            ...prev,
            email: {
              ...prev.email,
              indexError: 1,
              status: 'invalid',
            },
          }));
        }
      } else {
        console.log('error');
      }
    }
    if (step === 1) {
      if (data.name.status !== 'valid' || data.phone.status !== 'valid') {
        forms[1].forEach((input) => {
          console.log(data[input as keyof typeof data].status);

          if (data[input as keyof typeof data].status !== 'valid') {
            setData((prev) => ({
              ...prev,
              [input]: {
                ...prev[input as keyof typeof prev],
                indexError: 0,
                status: 'invalid',
              },
            }));
          }
        });
        return;
      }
      const res = await fetch('/api/v1/auth/send-verification', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          phone: data.phone.value,
        }),
      });
      if (res.status !== 200) {
        console.log('error');
        setLoading(false);
        return setData((prev) => ({
          ...prev,
          phone: {
            ...prev.phone,
            indexError: 0,
            status: 'invalid',
          },
        }));
      }
    }
    if (step === 2) {
      if (data.code.status !== 'valid') {
        return setData((prev) => ({
          ...prev,
          code: {
            ...prev.code,
            indexError: 0,
            status: 'invalid',
          },
        }));
      }
      setLoading(true);
      if (
        !(await signup({
          email: data.email.value,
          password: data.password.value,
          name: data.name.value,
          phone: data.phone.value,
          code: data.code.value,
        }))
      ) {
        setData((prev) => ({
          ...prev,
          code: {
            ...prev.code,
            indexError: 0,
            status: 'invalid',
          },
        }));
        return setLoading(false);
      }
      navigate('/', { replace: true });
      return setLoading(false);
    }
    if (step < 2) {
      setStep(step + 1);
    }
  };

  function handleBlur(e: React.FocusEvent<HTMLInputElement>) {
    const { name, value } = e.target;
    const input = inputs[name as keyof typeof inputs];
    const update = {
      status: 'empty',
      indexError: 0,
    };
    if (!value) return;
    update.status = input.validation(value) ? 'valid' : 'invalid';

    setData({
      ...data,
      [name]: {
        ...data[name as keyof Data],
        ...update,
      },
    });
  }
  function handleChange(e: React.ChangeEvent<HTMLInputElement>) {
    const { name, value } = e.target;
    const input = inputs[name as keyof typeof inputs];
    const isValid = input.validation(value);
    if (!isValid && value.length) return;
    const status = isValid ? 'valid' : 'empty';

    setData({
      ...data,
      [name]: {
        ...data[name as keyof Data],
        status,
        value,
      },
    });
  }

  console.log(data);

  return (
    <div>
      <div className="flex items-center mb-6">
        <img src={logo} alt="" className="w-24 drop-shadow-md mr-2.5" />
        <p className="text-xl">
          El Despacho <span className="block text-2xl">de Carkars</span>
        </p>
      </div>
      {/* <img src={logo} alt="" className="w-36 drop-shadow-md mb-4" /> */}
      <div className="flex flex-col max-h-[368px] min-h-[250px] relative w-[300px] bg-slate-50/50 shadow-md border border-slate-50 p-4 text-slate-700 overflow-hidden transition-all">
        {loading && (
          <div className="top-0 left-0 absolute w-full h-full bg-[#5d712d]/10 flex justify-center items-center z-10">
            <div className="animate-spin rounded-full h-28 w-28 border-t-[.25rem] border-b-[.25rem] border-[#5d712d]"></div>
          </div>
        )}
        <h1 className="font-semibold text-2xl text-center pb-4">
          Crear cuenta
        </h1>
        <AnimatePresence initial={false} mode="wait">
          <motion.div
            className="sticky"
            key={'form' + step}
            initial={{ opacity: 0, x: 300 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -300 }}
            transition={{ duration: 0.2 }}
          >
            {forms[step].map((inputName) => {
              const input = inputs[inputName as keyof typeof inputs];
              const error =
                input.errorMessages.length &&
                data[inputName as keyof Data].status === 'invalid'
                  ? input.errorMessages[
                      data[inputName as keyof Data].indexError
                    ]
                  : null;
              if (inputName === 'phone')
                console.log(error, data[inputName as keyof Data].status);

              return (
                <div key={input.name}>
                  <label htmlFor={input.name} className="block mb-2">
                    {input.label}
                  </label>
                  {input.type === 'password' ? (
                    <div className="flex items-center relative">
                      <input
                        type={showPassword ? 'text' : input.type}
                        name={input.name}
                        id={input.name}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        placeholder={input.placeholder}
                        className={`w-full p-2 border ${
                          styles[data[input.name as keyof Data].status]
                        } rounded-md focus:outline-none transition-all`}
                      />
                      <button
                        onClick={() => setShowPassword(!showPassword)}
                        className="group absolute right-0 h-full aspect-square transition-all flex items-center justify-center bg-slate-200/50 "
                      >
                        {showPassword ? (
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            fill="none"
                            viewBox="0 0 24 24"
                            strokeWidth={1.5}
                            stroke="currentColor"
                            className="w-6 h-6 opacity-60 group-hover:opacity-100 group-hover:drop-shadow transition-all"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              d="M2.036 12.322a1.012 1.012 0 010-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178z"
                            />
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                            />
                          </svg>
                        ) : (
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            fill="none"
                            viewBox="0 0 24 24"
                            strokeWidth={1.5}
                            stroke="currentColor"
                            className="w-6 h-6 opacity-60 group-hover:opacity-100 group-hover:drop-shadow transition-all"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              d="M3.98 8.223A10.477 10.477 0 001.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.45 10.45 0 0112 4.5c4.756 0 8.773 3.162 10.065 7.498a10.523 10.523 0 01-4.293 5.774M6.228 6.228L3 3m3.228 3.228l3.65 3.65m7.894 7.894L21 21m-3.228-3.228l-3.65-3.65m0 0a3 3 0 10-4.243-4.243m4.242 4.242L9.88 9.88"
                            />
                          </svg>
                        )}
                      </button>
                    </div>
                  ) : (
                    <input
                      type={input.type}
                      name={input.name}
                      id={input.name}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      placeholder={input.placeholder}
                      className={`w-full p-2 border ${
                        styles[data[input.name as keyof Data].status]
                      } rounded-md focus:outline-none`}
                    />
                  )}
                  <div className="flex relative justify-end h-8">
                    {input.errorMessages.length > 0 && (
                      <p
                        className={`text-red-500 absolute ${
                          data[input.name as keyof Data].status === 'invalid'
                            ? 'opacity-100'
                            : 'opacity-0'
                        } text-right text-sm mt-1`}
                      >
                        {error}.
                      </p>
                    )}
                    {!error && input.helpMessage && (
                      <p
                        className={`${
                          data[input.name as keyof Data].status === 'invalid'
                            ? 'text-red-500'
                            : 'text-slate-600/70'
                        } text-sm mt-1`}
                      >
                        {input.helpMessage}.
                      </p>
                    )}
                  </div>
                </div>
              );
            })}
          </motion.div>
        </AnimatePresence>
        <button
          onClick={handleClick}
          className="w-full p-2 bg-[#92c07a] hover:bg-[#839e3f] hover:shadow text-slate-50 font-semibold mt-8 rounded-md transition-all"
        >
          <span>Continuar</span>
        </button>
      </div>
      <div className="mt-4 px-4 flex w-[300px]">
        <Link
          to="/auth/login"
          className="w-full p-2 bg-slate-50/50 text-[#92c07a] hover:bg-slate-50 text-center hover:underline font-semibold rounded-md transition-all"
        >
          Iniciar sesin
        </Link>
      </div>
    </div>
  );
};

export default Signup;
