import { createContext, useContext, useEffect, useReducer } from 'react';
import { IChatContext, IChatState } from './types';
import chatReducer from './chat-reducer';
import io from 'socket.io-client';
import { AuthContext } from '../user/auth-state';

const initialState: IChatState = {
  connected: false,
  messages: [],
};

export const ChatContext = createContext({} as IChatContext);

const socket = io('');

const ChatState = ({ children }: { children: React.ReactNode }) => {
  const { user } = useContext(AuthContext);

  const [state, dispatch] = useReducer(chatReducer, initialState);

  useEffect(() => {
    socket.on('connect', () => {
      console.log('connected');
      handleConnection(true);
    });

    socket.on('disconnect', () => {
      console.log('disconnected');

      handleConnection(false);
    });

    socket.on('chat message', (message) => {
      setMessage(message);
    });
    socket.on('chat initializing', (messages) => {
      setMessages(messages);
    });

    return () => {
      socket.off('connect');
      socket.off('disconnect');
      socket.off('chat message');
    };
  }, []);

  useEffect(() => {
    if (user) {
      // sendMessage({
      //   text: 'Hello',
      //   to: 'all',
      // });
      socket.emit('chat initializing', user.token);
    }
  }, [user]);

  const sendMessage = (msg: object) => {
    const message = {
      ...msg,
      from: user?.id,
    };
    socket.emit('chat message', { token: user?.token, msg: message });
  };

  const setMessage = (message: object) => {
    dispatch({
      type: 'SET_MESSAGE',
      payload: message,
    });
  };
  const setMessages = (messages: object) => {
    dispatch({
      type: 'SET_MESSAGES',
      payload: messages,
    });
  };

  const handleConnection = (connection: boolean) => {
    dispatch({
      type: 'SET_CONNECTED',
      payload: connection,
    });
  };

  return (
    <ChatContext.Provider
      value={{
        connected: state.connected,
        messages: state.messages,
        sendMessage,
      }}
    >
      {children}
    </ChatContext.Provider>
  );
};

export default ChatState;
