import { useContext, useEffect, useState } from 'react';
import { AuthContext } from '../contexts/user/auth-state';
import { useNavigate } from 'react-router-dom';

const Orders = () => {
  const navigate = useNavigate();
  const { user, logout } = useContext(AuthContext);
  const [orders, setOrders] = useState<any>([]);
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    const gerOrders = async () => {
      const res = await fetch('/api/v1/orders', {
        method: 'GET',
        headers: {
          Authorization: `Bearer ${user?.token}`,
        },
      });

      if (res.ok) {
        const data = await res.json();
        setOrders(data.orders.reverse());
      } else {
        if (res.status == 401) {
          logout();
          navigate('/auth/login', { replace: true });
        }
      }
      setLoading(false);
    };

    gerOrders();
  }, []);
  if (loading)
    return (
      <div className="w-full h-full">
        <div className="top-0 left-0 w-full h-full flex justify-center items-center">
          <div className="animate-spin rounded-full h-28 w-28 border-t-[.25rem] border-b-[.25rem] border-[#5d712d]"></div>
        </div>
      </div>
    );
  return (
    <div className="flex flex-col">
      <h1 className="w-full text-xl text-center font-semibold">Pedidos</h1>
      <div className="flex justify-center  flex-grow-0 flex-wrap gap-4">
        {!orders.length ? (
          <div className="flex flex-col justify-center items-center">
            <h1 className="text-xl text-center font-semibold mb-4">
              No tienes pedidos
            </h1>
            <button
              onClick={() => {
                navigate(-1);
              }}
              className="py-1 px-2 flex items-center bg-slate-50/50 hover:bg-slate-50 hover:shadow-md text-slate-600 rounded-md shadow"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1.5}
                className="w-5 h-5 stroke-slate-600"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M9 15L3 9m0 0l6-6M3 9h12a6 6 0 010 12h-3"
                />
              </svg>
              <p className="ml-1">Volver</p>
            </button>
          </div>
        ) : (
          orders.map((order: any) => (
            <button
              onClick={() => navigate(`/orders/${order._id}`)}
              key={order._id}
              className="flex rounded flex-col border border-transparent   w-[300px]  hover:shadow-md hover:border-slate-400/50"
            >
              <div className="flex rounded flex-col p-4 bg-slate-200 w-[300px] h-[250px] hover:translate-x-2 hover:-translate-y-2 border border-slate-400/50 transition-all cursor-pointer">
                <div className="mb-4 text-slate-600 flex items-center justify-center">
                  <span className="border border-gray-600/50 mr-1 w-3.5 h-3.5 rounded-full bg-slate-400 shadow"></span>
                  <p className="font-semibold">En proceso</p>
                </div>
                <div className="flex text-sm justify-between text-slate-600 mb-4">
                  <p className="flex-1 text-center">
                    <span className="block font-semibold">Num. de Pedido</span>{' '}
                    {order.order_num}
                  </p>
                  {/* <p className="font-semibold w-full text-center">
                <span className="inline-block mr-1 w-2.5 h-2.5 rounded-full bg-green-400 shadow-md"></span>
                En progreso
              </p> */}
                  <p className="flex-1 text-center">
                    <span className="block font-semibold">Realizado el</span>
                    {new Date(order.createdAt).toLocaleDateString('es-MX')}
                  </p>
                </div>
                <div className="flex text-slate-600 text-sm">
                  <div className="flex-1 items-center mb-4 text-center">
                    <p>
                      <span className="block font-semibold">Productos</span>
                      {order.total_items}
                    </p>
                    {/* <p>65</p> */}
                  </div>
                  <div className="flex-1 items-center mb-4 text-center">
                    <p>
                      <span className="block  font-semibold">Total</span>${' '}
                      {order.amount.toFixed(2)}
                    </p>
                    {/* <p>$ 100.00</p> */}
                  </div>
                </div>
                <div className="flex text-slate-600 text-sm">
                  <p className="w-full text-center">
                    <span className="block font-semibold">
                      Direccin de entrega
                    </span>
                    {order.address}
                  </p>
                  {/* <p>Av San Francisco #3650</p> */}
                </div>
              </div>
            </button>
          ))
        )}
      </div>
    </div>
  );
};

export default Orders;
