import { useNavigate, useParams } from 'react-router-dom';
import outOfStock from '../assets/imgs/out-of-stock.png';
import { NotificationsContext } from '../contexts/notifications/notifications-state';
import { useContext, useEffect, useState } from 'react';
import { AuthContext } from '../contexts/user/auth-state';
import s from '../assets/imgs/s.png';
import ErrorPage from './error-page';

const url = '/images/';
const ProductDetails = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [product, setProduct] = useState<any>(null);
  const [error, setError] = useState<any>(false);
  const [variant, setVariant] = useState<string>('universal');
  // const product = useLoaderData() as any;
  const { user, AddProductToCart, logout } = useContext(AuthContext);
  const { addNotification } = useContext(NotificationsContext);
  console.log('product details');

  useEffect(() => {
    if (!user) return;
    const getProduct = async () => {
      try {
        const res = await fetch(`/api/v1/products/${id}`, {
          method: 'GET',
          headers: {
            Authorization: `Bearer ${user?.token}`,
          },
        });

        if (res.ok) {
          console.log(res.status);
          const data = await res.json();
          setProduct(data.product);
          if (data.product.classes.length == 1)
            setVariant(data.product.classes[0]);
        } else {
          if (res.status === 401) {
            await logout();
            navigate('/auth/login', { replace: true });
          }
          setError(true);
        }
      } catch (err) {
        console.log(err);
      }
    };
    getProduct();
  }, []);

  const handleAddToCart = async () => {
    try {
      if (product.classes.length && variant === 'universal') {
        return addNotification('Selecciona una clase', 'warn');
      }
      if (await AddProductToCart(product._id, variant)) {
        addNotification('Producto aadido al carrito', 'success');
        navigate('/cart');
      } else {
        addNotification('Error al aadir el producto', 'error');
      }
    } catch (err) {
      console.log(err);
    }
    // addNotification('Producto aadido al carrito', 'success');
  };
  if (error) return <ErrorPage />;
  if (!product) return null;

  return (
    <div className="flex flex-col justify-center z-10 !flex-nowrap items-center h-full w-full text-slate-600">
      <h1 className="text-2xl text-slate-700 font-semibold bg-slate-200 py-1.5 px-5 shadow-md rounded border-[0.5px] border-slate-400/50">
        Detalles del Producto
      </h1>
      <div className=" text-slate-700 flex w-[80%] min-h-[300px] bg-slate-200 border border-slate-300 shadow-md p-2">
        <div
          className="h-[300px] drop-shadow-md w-[300px] shrink-0 mr-4 flex justify-center items-center"
          style={{
            backgroundImage: `url(${url}empty.png)`,
            backgroundSize: '95%',
            backgroundRepeat: 'no-repeat',
          }}
        >
          <img
            src={`${url + product._id}.png`}
            className="h-[95%] drop-shadow-md"
            onError={({ currentTarget }) => {
              currentTarget.onerror = null; // prevents looping
              currentTarget.src = `${url}notImagen.png`;
            }}
          />
        </div>
        <div className="p-4 flex flex-col w-full">
          <div className="flex justify-start items-center mb-4">
            <p className="font-semibold text-xl">{product.name}</p>
            <p className="font-semibold text-xs first-letter:capitalize mx-2 rounded bg-slate-50/50 px-2 py-1 shadow-sm">
              {product.rarity}
            </p>
          </div>
          <p className="mb-4">{product.description}</p>
          <div className="flex justify-start mb-4 flex-wrap gap-3">
            <div className="flex justify-start shadow-sm bg-slate-50/50 rounded-md">
              <p className="text-sm first-letter:capitalize  py-1 px-2">
                {product.weight}u
              </p>
            </div>
            <div className="flex justify-start py-1 px-2 shadow-sm bg-slate-50/50 rounded-md">
              {product.effects.map((effect: string, idx: number) => (
                <>
                  <p key={effect} className="text-sm first-letter:capitalize">
                    {effect}
                  </p>
                  {idx !== product.effects.length - 1 && (
                    <span className="text-slate-400 px-1 text-sm">|</span>
                  )}
                </>
              ))}
            </div>

            <div
              className={`flex ${
                !product.side_effects.length && 'hidden'
              } justify-start shadow-sm bg-slate-50/50 py-1 px-2 rounded-md`}
            >
              {product.side_effects.map((effect: string, idx: number) => (
                <>
                  <p key={effect} className="text-sm first-letter:capitalize">
                    {effect}
                  </p>
                  {idx !== product.side_effects.length - 1 && (
                    <span className="text-slate-400 px-1 text-sm">|</span>
                  )}
                </>
              ))}
            </div>
            <div className="flex justify-start py-1 px-2 items-center shadow-sm bg-slate-50/50 rounded-md">
              {product.origins.map((effect: string, idx: number) => (
                <>
                  <p key={effect} className="text-sm first-letter:capitalize">
                    {effect}
                  </p>
                  {idx !== product.origins.length - 1 && (
                    <span className="text-slate-400 px-1 text-sm">|</span>
                  )}
                </>
              ))}
            </div>
          </div>
          <div className="flex mb-4 flex-1 items-start">
            {product.classes.length > 1 ? (
              <select
                onChange={(e) => {
                  setVariant(e.target.value);
                }}
                name="_class"
                id="_class"
                // className="flex px-2 py-2 justify-start border-[.1px] box-border focus:outline-none border-[#92c07a] focus:border-2 transition-all shadow-md bg-slate-50/50 rounded-md"
                className="
                flex px-2.5 py-1.5 justify-start border border-slate-400/50 shadow-md bg-slate-50/50 rounded-md
                focus:outline-none transition-all "
              >
                <option value="">Selecciona t clase</option>
                {product.classes.map((_class: string) => {
                  const c = _class[0].toUpperCase() + _class.slice(1);
                  return (
                    <option key={_class} value={_class} className="capitalize">
                      {c}
                    </option>
                  );
                })}
              </select>
            ) : (
              <p
                className="
                flex px-2.5 py-1.5 capitalize justify-start border border-slate-400/50 shadow-md bg-slate-50/50 rounded-md
                focus:outline-none transition-all "
              >
                {variant}
              </p>
            )}
          </div>
          <button
            onClick={handleAddToCart}
            className="text-lg rounded shadow-md bg-[#92c07a] hover:bg-[#73912c] transition-all mb-0 py-2 px-4 text-slate-50 font-semibold"
          >
            Aadir al carrito  $ {product.price.toFixed(2)}
          </button>
        </div>
        {/* {products &&
          products.map((product) => {
            if (product.rarity === 'extraordinario') {
              return <Card key={product._id} product={product} />;
            }
          })} */}
      </div>
      <button
        onClick={() => {
          navigate(-1);
        }}
        className="py-1 px-2 flex items-center bg-slate-50/50 hover:bg-slate-50 hover:shadow-md text-slate-600 rounded-md shadow"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          strokeWidth={1.5}
          className="w-5 h-5 stroke-slate-600"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            d="M9 15L3 9m0 0l6-6M3 9h12a6 6 0 010 12h-3"
          />
        </svg>
        <p className="ml-1">Volver</p>
      </button>
    </div>
  );
};

export default ProductDetails;
