import { Outlet, useNavigate } from 'react-router-dom';
import Header from '../header';
import Aside from '../aside';
import bg from '../../assets/imgs/background.png';
import Notification from '../notification';
import { useContext, useEffect, useState } from 'react';
import { NotificationsContext } from '../../contexts/notifications/notifications-state';
import { AnimatePresence, motion } from 'framer-motion';
import { AuthContext } from '../../contexts/user/auth-state';

const variants = {
  show: { opacity: 1, x: 0, scale: 1 },
  hide: { opacity: 0, scale: 0.5, transition: { duration: 0.2 } },
};

const Layout = () => {
  const navigate = useNavigate();
  const { user, initializing, logout } = useContext(AuthContext);
  const { notifications } = useContext(NotificationsContext);
  const [profile, setProfile] = useState(false);

  useEffect(() => {
    if (!user && !initializing) {
      navigate('/auth/login', { replace: true });
    }
  }, [initializing, user]);

  return (
    <div
      id="mainLayout"
      className="layout bg-cover"
      style={{ backgroundImage: `url(${bg})` }}
    >
      <div className="w-64 notifications absolute z-50 right-0 top-16 mt-1 mr-3">
        {/* <Outlet path="notifications/*" /> */}
        <AnimatePresence initial={true}>
          {notifications.map((notification) => (
            <Notification
              key={notification.id}
              id={notification.id}
              message={notification.message}
              type={notification.type}
            />
          ))}
        </AnimatePresence>
      </div>
      <div className="absolute right-2 top-16 z-50">
        <AnimatePresence initial={false}>
          {profile && (
            <motion.div
              className="bg-slate-50/90 p-4 rounded-md shadow-md w-[300px]"
              layout
              variants={variants}
              initial={{ opacity: 0, x: 300, scale: 0.3 }}
              animate={'show'}
              exit={'hide'}
              // onAnimationComplete={(definition) =>
              //   setTimeout(() => {
              //     definition == 'show' && removeNotification(id);
              //   }, 3000)
              // }
            >
              <h1 className="text-2xl text-slate-700 font-semibold text-center mb-4">
                Mi Perfil
              </h1>
              <div className="flex justify-between mb-2">
                <p className="font-semibold text-slate-700">ID: </p>
                <p className="text-slate-600">{user?.id}</p>
              </div>
              <div className="flex justify-between mb-2">
                <p className="font-semibold text-slate-700">Nombre: </p>
                <p className="text-slate-600">{user?.name}</p>
              </div>
              <div className="flex justify-between mb-2">
                <p className="font-semibold text-slate-700">Telfono: </p>
                <p className="text-slate-600">{user?.phone}</p>
              </div>
              <div className="flex justify-between mb-2">
                <p className="font-semibold text-slate-700">Email: </p>
                <p className="text-slate-600">{user?.email}</p>
              </div>
              <div className="flex justify-between mb-2">
                <p className="font-semibold text-slate-700">Rol: </p>
                <p className="text-slate-600">
                  {user?.role === 'user'
                    ? 'Usuario'
                    : user?.role === 'admin' && 'Administrador'}
                </p>
              </div>
              <div className="group flex px-2 py-5 hover:shadow-md justify-center items-center transition-all bg-slate-200 rounded mt-6 relative">
                <button
                  onClick={() => {
                    setProfile(false);
                    navigate('/orders');
                  }}
                  className="text-slate-600 group-hover:translate-x-1 group-hover:-translate-y-1 group-hover:bg-slate-400 group-hover:shadow-md group-hover:text-slate-50 flex w-full p-2 transition-all justify-center items-center bg-slate-200 rounded absolute top-0"
                >
                  <p>Pedidos</p>
                </button>
              </div>
              <div
                onClick={() => {
                  logout();
                }}
                className="group flex w-full px-2 py-5 hover:shadow-md justify-center items-center transition-all  rounded mt-3 relative"
              >
                <button className="text-slate-600 group-hover:translate-x-1 group-hover:-translate-y-1 border border-slate-400 group-hover:bg-slate-400 group-hover:shadow-md group-hover:text-slate-50 flex w-full p-2 transition-all justify-center items-center  rounded absolute top-0">
                  <p>Cerrar sesin</p>
                </button>
              </div>
            </motion.div>
          )}
        </AnimatePresence>
      </div>

      <Header
        handleProfile={() => {
          setProfile(!profile);
        }}
        profile={profile}
      />
      <div className="flex flex-1 p-4 asd">
        <Aside />
        <main>{user && <Outlet />}</main>
      </div>
    </div>
  );
};

export default Layout;
