import { useContext, useEffect, useState } from 'react';
import { ChatContext } from '../contexts/chat/chat-state';
import userEvent from '@testing-library/user-event';
import { AuthContext } from '../contexts/user/auth-state';

const Chat = () => {
  const { messages, sendMessage } = useContext(ChatContext);
  const { user } = useContext(AuthContext);
  const [data, setData] = useState({
    to: '',
    text: '',
  });

  useEffect(() => {
    if (user) {
      setData({
        ...data,
        to: user.role === 'admin' ? 'all' : 'admin',
      });
    }
  }, [user]);
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setData({
      ...data,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    sendMessage(data);
    setData({
      ...data,
      text: '',
    });
  };

  return (
    <div className="flex-1 flex flex-col min-w-[250px] h-full bg-slate-50/50">
      <div className="bg-[#92c07abf] justify-center flex p-2">
        <h2 className="font-semibold text-xl text-white drop-shadow">Chat</h2>
      </div>
      <div className="flex flex-col-reverse h-full w-full p-2 overflow-y-scroll">
        {messages.length > 0 &&
          messages.map((message) => {
            let style = '';
            const select =
              message.from === data.to && user?.role === 'admin'
                ? 'border-sky-400 transition-all'
                : '';
            if (user?.role === 'admin') {
              if (message.from === 'admin') {
                style = 'self-end text-end';
              } else {
                style = 'self-start';
              }
            } else if (message.from === user?.id) {
              style = 'self-end text-end';
            } else {
              style = 'self-start';
            }
            return (
              <button
                name="to"
                onClick={() => {
                  if (message.from === 'admin' || user?.role !== 'admin')
                    return;
                  setData({
                    ...data,
                    to: message.from,
                  });
                }}
                key={message._id}
                className={`${select} text-start rounded ${style} shadow-sm border border-slate-400/25 bg-slate-50/75 max-w-[90%] my-1 py-1 px-2 text-sm `}
              >
                <p
                  className={`${
                    data.to === message.from &&
                    user?.role === 'admin' &&
                    'text-sky-600'
                  } first-letter:capitalize font-semibold text-slate-600 text-xs transition-all`}
                >
                  {`${
                    message.from === user?.id ||
                    (message.from === 'admin' && user?.role === 'admin')
                      ? 'T'
                      : message.from
                  }`}{' '}
                  {user?.role === 'admin' && message.from == 'admin' && (
                    <span>{` > ${
                      message.to === 'all' ? 'Todos' : message.to
                    }`}</span>
                  )}
                  {message.from == user?.id && (
                    <span>{` > ${
                      message.to === 'all' ? 'Todos' : message.to
                    }`}</span>
                  )}
                </p>
                <p className="text-slate-700">
                  <span className="text-xs text-slate-600">
                    {new Date(message.createdAt).toLocaleDateString('es-MX', {
                      year: '2-digit',
                      day: '2-digit',
                      month: '2-digit',
                    }) + ' - '}
                  </span>
                  {message.text}
                </p>
              </button>
            );
          })}
      </div>
      <div className="flex flex-col relative border-t shadow-md border-slate-400/50">
        {user?.role === 'admin' && (
          <div className="px-2 py-1 text-sm flex justify-between">
            <p>Para: {data.to === 'all' ? 'Todos' : data.to}</p>
            {data.to !== 'all' && (
              <button
                onClick={() => {
                  setData({
                    ...data,
                    to: 'all',
                  });
                }}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="w-5 aspect-square"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M6 18L18 6M6 6l12 12"
                  />
                </svg>
              </button>
            )}
          </div>
        )}

        <form className="flex items-center relative" onSubmit={handleSubmit}>
          <input
            type="text"
            name="text"
            onChange={handleChange}
            value={data.text}
            className="w-full border border-transparent bg-white/75 p-2 pr-10 focus:outline-none shadow transition-colors focus:border-sky-400/75"
            placeholder="Escribe un mensaje"
          />
          <button className="absolute right-0 px-2">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              className={`w-6 h-6 -rotate-[90deg] transition-all ${
                data.text
                  ? 'stroke-slate-500 hover:stroke-sky-400 hover:drop-shadow fill-slate-50'
                  : 'stroke-slate-400'
              }`}
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M6 12L3.269 3.126A59.768 59.768 0 0121.485 12 59.77 59.77 0 013.27 20.876L5.999 12zm0 0h7.5"
              />
            </svg>
          </button>
        </form>
      </div>
    </div>
  );
};

export default Chat;
