import { Action, State, User } from './types';

export default function (state: State, action: Action): State {
  const { type, payload } = action;

  switch (type) {
    case 'SET_USER':
      return {
        ...state,
        user: payload as User | null,
        initializing: false,
      };
    case 'SET_TOKEN':
      if (!state.user || typeof payload !== 'string') {
        console.log('not user or invalid token');
        return state;
      }
      return {
        ...state,
        user: {
          ...state.user,
          token: payload as string,
        },
      };
    case 'SET_LOADING':
      if (typeof payload !== 'boolean') return state;
      return {
        ...state,
        loading: payload,
      };
    case 'SET_ERROR_MESSAGE':
      if (typeof payload !== 'string') return state;
      return {
        ...state,
        errorMessage: payload,
      };
    case 'SET_CART':
      if (!state.user) return state;
      return {
        ...state,
        user: {
          ...state.user,
          cart: payload,
        },
      };
    default:
      return state;
  }
}
