import React, { useContext, useState } from 'react';
import logo from '../../assets/imgs/logo.png';
import { Link, useNavigate } from 'react-router-dom';
import { AuthContext } from '../../contexts/user/auth-state';

const Login = () => {
  const navigate = useNavigate();
  const { user, login } = useContext(AuthContext);

  const [data, setData] = useState({
    email: '',
    password: '',
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    try {
      e.preventDefault();

      setLoading(true);
      const { status, message } = await login(data);
      if (status) {
        navigate('/', { replace: true });
      } else {
        setLoading(false);
        setError(true);
      }
    } catch (err) {
      console.log(err);
      setLoading(false);
    }
  };
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setData({ ...data, [e.target.name]: e.target.value });
  };

  return (
    <div>
      <div className="flex items-center mb-6">
        <img src={logo} alt="" className="w-24 drop-shadow-md mr-2.5" />
        <p className="text-xl">
          El Despacho <span className="block text-2xl">de Carkars</span>
        </p>
      </div>
      {/* <img src={logo} alt="" className="w-36 drop-shadow-md mb-4" /> */}
      <form
        onSubmit={handleSubmit}
        className="relative min-w-[300px] min-h-20 bg-slate-50/50 shadow-md border border-slate-50 p-4 text-slate-700"
      >
        {loading && (
          <div className="top-0 left-0 absolute w-full h-full bg-[#5d712d]/10 flex justify-center items-center">
            <div className="animate-spin rounded-full h-28 w-28 border-t-[.25rem] border-b-[.25rem] border-[#5d712d]"></div>
          </div>
        )}
        <h1 className="font-semibold text-2xl text-center pb-2">Login</h1>
        {error && (
          <p className="text-red-400 text-sm font-semibold text-center pb-2">
            Email o contrasea incorrectos.
          </p>
          // <div
          //   className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative mb-4"
          //   role="alert"
          // >
          //   <strong className="font-bold">Error!</strong>
          //   <span className="block sm:inline">
          //     {' '}
          //     Email o contrasea incorrectos
          //   </span>
          // </div>
        )}
        <div className="flex flex-col mb-4">
          <label className="mb-1 font-semibold" htmlFor="">
            Email
          </label>
          <input
            type="text"
            name="email"
            value={data.email}
            onChange={handleChange}
            className="p-2 mx-1 rounded-md border border-transparent focus:border-[#92c07a] focus:outline-none focus:shadow-md transition-shadow"
            placeholder="Ingrese su email"
          />
        </div>
        <div className="flex flex-col ">
          <label className="mb-1 font-semibold" htmlFor="">
            Contrasea
          </label>
          <input
            type="password"
            name="password"
            onChange={(e) => handleChange(e)}
            className="p-2 mx-1 rounded-md border border-transparent focus:border-[#92c07a] focus:outline-none focus:shadow-md transition-shadow"
            placeholder="Ingrese su contrasea"
          />
        </div>
        <button className="w-full p-2 bg-[#92c07a] hover:bg-[#839e3f] hover:shadow text-slate-50 font-semibold mt-8 rounded-md transition-all">
          <span>Acceder</span>
          {/* <div className="fixed top-0 right-0 h-screen w-screen z-50 flex justify-center items-center">
          </div> */}
        </button>
      </form>
      <div className="mt-4 px-4 flex w-[300px]">
        <Link
          to="/auth/signup"
          className="w-full p-2 bg-slate-50/50 text-[#92c07a] hover:bg-slate-50 text-center hover:underline font-semibold rounded-md transition-all"
        >
          Registrarse
        </Link>
      </div>
    </div>
  );
};

export default Login;
