import { useContext, useEffect, useState } from 'react';
import { AuthContext } from '../contexts/user/auth-state';
import { Link, useNavigate } from 'react-router-dom';
import outofstock from '../assets/imgs/out-of-stock.png';
const url = '/images/';
const Cart = () => {
  const navigate = useNavigate();
  const { user, logout, setCart: setUserCar } = useContext(AuthContext);
  const [cart, setCart] = useState<any>(null);
  const [address, setAddress] = useState<string>('');
  const [shipping, setShipping] = useState<number>(
    Math.floor(Math.random() * (100 - 10) + 10),
  );
  const [loading, setLoading] = useState<boolean>(true);
  useEffect(() => {
    if (!user) return;
    const getCartProducts = async () => {
      try {
        const res = await fetch(`/api/v1/cart/${user?.cart._id}`, {
          method: 'GET',
          headers: {
            Authorization: `Bearer ${user?.token}`,
          },
        });
        if (res.ok) {
          const data = await res.json();
          let total = 0;
          data.cart.products.forEach((item: any) => {
            total += item.product.price * item.quantity;
          });
          data.cart.products.reverse();
          setCart({ ...data.cart, amount: total });
        } else {
          if (res.status === 401) {
            logout();
            navigate('/auth/login', { replace: true });
          }
          throw new Error('Error');
        }
        setLoading(false);
      } catch (err) {
        console.log(err);
      }
    };
    getCartProducts();
  }, []);

  const updateProduct = async (action: string, product_id: string) => {
    try {
      const res = await fetch(
        `/api/v1/cart/${user?.cart._id}/${action}/${product_id}`,
        {
          method: 'PUT',
          headers: {
            Authorization: `Bearer ${user?.token}`,
          },
        },
      );

      if (res.ok) {
        const data = await res.json();
        let total = 0;
        data.cart.products.forEach((item: any) => {
          total += item.product.price * item.quantity;
        });
        data.cart.products.reverse();

        setCart({ ...data.cart, amount: total });
      } else {
        if (res.status === 401) {
          logout();
          navigate('/auth/login', { replace: true });
        }
        throw new Error('Error');
      }
    } catch (err) {
      console.log(err);
    }
  };

  const handleClick = async () => {
    try {
      const res = await fetch(`/api/v1/orders`, {
        method: 'POST',
        headers: {
          Authorization: `Bearer ${user?.token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          cartId: user?.cart._id,
          address,
          shipping,
        }),
      });

      if (res.ok) {
        const data = await res.json();
        setUserCar({
          ...user?.cart,
          products: [],
          total_products: 0,
          amount: 0,
        });
        navigate('/orders', { replace: true });
        // navigate('/orders', { replace: true });
      } else {
        if (res.status === 401) {
          logout();
          navigate('/auth/login', { replace: true });
        }
        throw new Error('Error');
      }
    } catch (err) {
      console.log(err);
    }
  };

  if (loading)
    return (
      <div className="w-full h-full">
        <div className="top-0 left-0 w-full h-full flex justify-center items-center">
          <div className="animate-spin rounded-full h-28 w-28 border-t-[.25rem] border-b-[.25rem] border-[#5d712d]"></div>
        </div>
      </div>
    );

  return (
    <div className="flex w-full">
      <div className="flex flex-1 flex-col self-start">
        {!cart.products.length && (
          <div className="flex flex-col items-center">
            <h1 className="text-2xl font-bold my-4">T carrito est vaco</h1>
            <Link
              to={'/'}
              className="py-1 px-2 flex items-center bg-slate-50/50 hover:bg-slate-50 hover:shadow-md text-slate-600 rounded-md shadow"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1.5}
                className="w-5 h-5 stroke-slate-600"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M9 15L3 9m0 0l6-6M3 9h12a6 6 0 010 12h-3"
                />
              </svg>
              <p className="ml-1">Volver</p>
            </Link>
          </div>
        )}
        {cart.products.map(
          (
            {
              product,
              quantity,
              variant,
              _id,
            }: { product: any; quantity: number; variant: string; _id: string },
            index: number,
          ) => (
            <div
              key={_id}
              className={`flex border bg-slate-200 border-slate-400/50 shadow-md ${
                index < cart.products.length - 1 && 'mb-4'
              } flex-1 self-start`}
            >
              <div
                className="h-36 w-36 flex justify-center items-center"
                style={{
                  backgroundImage: `url(${url}empty.png)`,
                  backgroundRepeat: 'no-repeat',
                  backgroundSize: '95%',
                  backgroundPosition: 'center',
                }}
              >
                <img
                  src={`${url + product._id}.png`}
                  alt=""
                  className="h-[95%] w-[95%] object-contain"
                  onError={({ currentTarget }) => {
                    currentTarget.onerror = null; // prevents looping
                    currentTarget.src = `${url}notImagen.png`;
                  }}
                />
              </div>
              <div className="my-2 bg-slate-50/50 w-[2px]" />
              <div className="flex flex-col p-4 w-[300px]">
                <h1 className="text-xl font-bold">{product.name}</h1>
                <p className="first-letter:capitalize text-slate-600/70 mt-1">
                  {variant}
                </p>
              </div>
              <div className="my-2 bg-slate-50/50 w-[2px]" />
              <div className="flex flex-col p-4 justify-center relative">
                <div className="flex items-center">
                  <button
                    onClick={() => updateProduct('remove', _id)}
                    className="w-8 h-8 mr-2 shadow-md rounded-full border border-slate-200 bg-slate-50/20 hover:bg-slate-50 transition-all flex justify-center items-center"
                  >
                    -
                  </button>
                  <span className="w-8 h-8 shadow-md rounded-full border border-slate-200 bg-slate-50/75 text-sm flex justify-center items-center">
                    {quantity}
                  </span>
                  <button
                    onClick={() => updateProduct('add', _id)}
                    className="w-8 h-8 ml-2 rounded-full shadow-md border border-slate-200 border-slate-200 bg-slate-50/20 hover:bg-slate-50 flex justify-center items-center"
                  >
                    +
                  </button>
                </div>
                <button
                  onClick={() => updateProduct('delete', _id)}
                  className="
                    absolute flex bottom-4 py-1 pr-1.5 rounded-full text-xs opacity-75 font-semibold border border-red-400 text-red-400 left-[50%] translate-x-[-50%]
                    hover:bg-red-400 hover:text-slate-50 hover:opacity-100 hover:shadow-md transition-all"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className="w-4 h-4 ml-1.5 mr-1"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0"
                    />
                  </svg>

                  <p>Eliminar</p>
                </button>
              </div>
              <div className="my-2 bg-slate-50/50 w-[2px]" />
              <div className="flex p-4 items-center justify-end w-[116px]">
                <p>$ {(product.price * quantity).toFixed(2)}</p>
                {/* <p className="font-semibold">$ 999.00</p> */}
              </div>
            </div>
          ),
        )}
      </div>
      {cart.products.length > 0 && (
        <div className="flex flex-1 self-start sticky top-6 bg-slate-200 shadow-md border border-slate-400/50 p-4">
          <div className="flex flex-col flex-1">
            <h1 className="text-xl font-bold text-center">Resumen</h1>
            <div className="flex flex-col mt-2 px-4">
              <div className="flex justify-center mb-4">
                <p className="">{cart.total_products} Productos</p>
                {/* <p className="text-slate-600/70">Productos</p> */}
              </div>
              <div className="flex justify-between mb-1">
                <p className="text-slate-600/70">Subtotal</p>
                <p className="font-semibold">$ {cart.amount.toFixed(2)}</p>
              </div>
              <div className="flex justify-between mb-1">
                <p className="text-slate-600/70">Envio</p>
                <p className="font-semibold">$ {shipping.toFixed(2)}</p>
              </div>
              <div className="flex justify-between mb-1">
                <p className="text-slate-600/70">Total</p>
                <p className="font-semibold">
                  $ {(cart.amount + shipping).toFixed(2)}
                </p>
              </div>
              <div className="flex flex-col justify-between mb-1 mt-4">
                <label className="text-slate-600 mb-1">
                  Ingresa una direccin
                </label>
                <input
                  type="text"
                  onChange={(e) => setAddress(e.target.value)}
                  value={address}
                  className="
                    py-1.5 px-2 bg-slate-50/50 border border-gray-400/50
                    focus:outline-none focus:shadow focus:border-gray-400/75 transition-colors
                  "
                  placeholder="Calle, nmero, colonia, ciudad, estado, cdigo postal"
                />
              </div>
              <button
                onClick={handleClick}
                className="w-full p-2 bg-[slate-200] border border-[#779139] rounded-md mt-8 hover:bg-[#779139] hover:text-slate-50 font-semibold hover:shadow-md"
              >
                <p>Realizar compra</p>
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Cart;
