import { useContext, useEffect, useState } from 'react';
import { AuthContext } from '../contexts/user/auth-state';
import { Link, useNavigate, useParams } from 'react-router-dom';
import outofstock from '../assets/imgs/out-of-stock.png';
const url = '/images/';
const Order = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { user, logout } = useContext(AuthContext);
  const [order, setOrder] = useState<any>(null);
  const [loading, setLoading] = useState<boolean>(true);
  useEffect(() => {
    if (!user) return;
    const getorderProducts = async () => {
      try {
        const res = await fetch(`/api/v1/orders/${id}`, {
          method: 'GET',
          headers: {
            Authorization: `Bearer ${user?.token}`,
          },
        });
        if (res.ok) {
          const data = await res.json();

          data.order.items.reverse();
          setOrder(data.order);
        } else {
          if (res.status === 401) {
            logout();
            navigate('/auth/login', { replace: true });
          }
          throw new Error('Error');
        }
        setLoading(false);
      } catch (err) {
        console.log(err);
      }
    };
    getorderProducts();
  }, []);

  if (loading)
    return (
      <div className="w-full h-full">
        <div className="top-0 left-0 w-full h-full flex justify-center items-center">
          <div className="animate-spin rounded-full h-28 w-28 border-t-[.25rem] border-b-[.25rem] border-[#5d712d]"></div>
        </div>
      </div>
    );

  if (!order) return <h1>Loading...</h1>;

  return (
    <div className="flex w-full flex-wrap justify-center">
      <div className="w-full text-center ">
        <h1 className="text-2xl font-semibold text-slate-700">
          Detalles del Pedido
        </h1>
        <p className="font-semibold text-slate-700">{order._id}</p>
      </div>
      <div className="flex flex-col self-start">
        {!order.items.length && (
          <div className="flex flex-col items-center">
            <h1 className="text-2xl font-bold my-4">T carrito est vaco</h1>
            <Link
              to={'/'}
              className="py-1 px-2 flex items-center bg-slate-50/50 hover:bg-slate-50 hover:shadow-md text-slate-600 rounded-md shadow"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1.5}
                className="w-5 h-5 stroke-slate-600"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M9 15L3 9m0 0l6-6M3 9h12a6 6 0 010 12h-3"
                />
              </svg>
              <p className="ml-1">Volver</p>
            </Link>
          </div>
        )}
        {order.items.map((item: any, index: number) => (
          <div
            key={item._id}
            className={`flex border bg-slate-200 border-slate-400/50 shadow-md ${
              index < order.items.length - 1 && 'mb-4'
            } flex-1 self-start`}
          >
            <div
              className="h-36 w-36 flex justify-center items-center"
              style={{
                backgroundImage: `url(${url}empty.png)`,
                backgroundRepeat: 'no-repeat',
                backgroundSize: '90%',
                backgroundPosition: 'center',
              }}
            >
              <img
                src={`${url + item.image}.png`}
                alt=""
                className="h-[95%] w-[95%] object-contain"
                onError={({ currentTarget }) => {
                  currentTarget.onerror = null; // prevents looping
                  currentTarget.src = `${url}notImagen.png`;
                }}
              />
            </div>
            <div className="my-2 bg-slate-50/50 w-[2px]" />
            <div className="flex flex-col p-4 w-[300px]">
              <h1 className="text-xl font-bold">{item.name}</h1>
              <p className="first-letter:capitalize text-slate-600/70 mt-1">
                {item.variant}
              </p>
            </div>
            <div className="my-2 bg-slate-50/50 w-[2px]" />
            <div className="flex flex-col p-4 justify-center relative">
              <div className="flex flex-col h-full items-center justify-around w-28">
                <span className="py-1.5 px-4 shadow rounded-full border border-slate-200 bg-slate-50/50 text-sm flex justify-center items-center">
                  {item.quantity}
                </span>
                <span className="py-1.5 px-4  shadow rounded-full border border-slate-200 bg-slate-50/50 text-sm flex justify-center items-center">
                  $ {(item.price * item.quantity).toFixed(2)}
                </span>
                {/* <p>$ {(item.price * quantity).toFixed(2)}</p> */}
              </div>
            </div>
          </div>
        ))}
      </div>
      {order && (
        <div
          onClick={() => navigate(`/orders/${order._id}`)}
          key={order._id}
          className="flex rounded flex-col p-4 bg-slate-200 border border-slate-400/50 shadow-md w-[300px] h-[250px]"
        >
          <div className="mb-4 text-slate-600 flex items-center justify-center">
            <span className="border border-gray-600/50 mr-1 w-3.5 h-3.5 rounded-full bg-slate-400 shadow"></span>
            <p className="font-semibold">En proceso</p>
          </div>
          <div className="flex text-sm justify-between text-slate-600 mb-4">
            <p className="flex-1 text-center">
              <span className="block font-semibold">Num. de Pedido</span>{' '}
              {order.order_num}
            </p>
            {/* <p className="font-semibold w-full text-center">
              <span className="inline-block mr-1 w-2.5 h-2.5 rounded-full bg-green-400 shadow-md"></span>
              En progreso
            </p> */}
            <p className="flex-1 text-center">
              <span className="block font-semibold">Realizado el</span>
              {new Date(order.createdAt).toLocaleDateString('es-MX')}
            </p>
          </div>
          <div className="flex text-slate-600 text-sm">
            <div className="flex-1 items-center mb-4 text-center">
              <p>
                <span className="block font-semibold">Productos</span>
                {order.total_items}
              </p>
              {/* <p>65</p> */}
            </div>
            <div className="flex-1 items-center mb-4 text-center">
              <p>
                <span className="block  font-semibold">Total</span>${' '}
                {order.amount.toFixed(2)}
              </p>
              {/* <p>$ 100.00</p> */}
            </div>
          </div>
          <div className="flex text-slate-600 text-sm">
            <p className="w-full text-center">
              <span className="block font-semibold">Direccin de entrega</span>
              {order.address}
            </p>
            {/* <p>Av San Francisco #3650</p> */}
          </div>
        </div>
      )}
    </div>
  );
};

export default Order;
