import { Outlet, useNavigate } from 'react-router-dom';
import { useContext, useEffect } from 'react';
import bg from '../../assets/imgs/background.png';
import { AuthContext } from '../../contexts/user/auth-state';
import { NotificationsContext } from '../../contexts/notifications/notifications-state';
import { AnimatePresence } from 'framer-motion';
import Notification from '../notification';

const Layout = () => {
  const navigate = useNavigate();
  const { user, initializing, errorMessage, setErrorMessage } =
    useContext(AuthContext);
  const { notifications, addNotification } = useContext(NotificationsContext);

  useEffect(() => {
    if (user && !initializing) {
      navigate('/', { replace: true });
    }
    if (errorMessage) {
      addNotification(errorMessage, 'error');
      setErrorMessage('');
    }
  }, [initializing, user]);

  return (
    <div
      id="authLayout"
      className="layout bg-cover"
      style={{ backgroundImage: `url(${bg})` }}
    >
      <div className="w-64 notifications absolute z-50 right-0 top-20 m-3">
        {/* <Outlet path="notifications/*" /> */}
        <AnimatePresence initial={true}>
          {notifications.map((notification) => (
            <Notification
              key={notification.id}
              id={notification.id}
              message={notification.message}
              type={notification.type}
            />
          ))}
        </AnimatePresence>
      </div>
      <main className="m-0">
        <Outlet />
      </main>
    </div>
  );
};

export default Layout;
