import { useNavigate } from 'react-router-dom';
import e from '../assets/imgs/e.png';
interface Props {
  product: { [key: string]: string | number };
}
const url = '/images/';
const Card = ({ product }: Props) => {
  const navigate = useNavigate();

  async function handleClick(e: React.MouseEvent<HTMLButtonElement>) {
    e.preventDefault();
    navigate(`/product/${product._id}`);
  }
  return (
    <button
      onClick={handleClick}
      className="p-0.5 shadow-sm w-[200px] bg-slate-100 hover:shadow-xl transition-all cursor-pointer"
    >
      <div
        className="w-full aspect-square bg-slate-200"
        style={{
          backgroundImage: `url(${e})`,
          backgroundSize: '95%',
          backgroundPosition: 'center',
          backgroundRepeat: 'no-repeat',
        }}
      >
        <img
          src={`${url + product._id}.png`}
          alt=""
          className="h-[190px] w-[190px] p-[3px] object-contain"
          onError={({ currentTarget }) => {
            currentTarget.onerror = null; // prevents looping
            currentTarget.src = `${url}notImagen.png`;
          }}
        />
      </div>
      <div
        className="flex-col justify-center h-[55px] items-center text-center flex bg-slate-50/50 p-2"
        style={{ lineHeight: 'normal' }}
      >
        <p>{product.name}</p>
        {/* <p>Vara de los amantes</p> */}
      </div>
    </button>
  );
};

export default Card;
