import { Action, IChatState } from './types';

export default function (state: IChatState, action: Action): IChatState {
  const { type, payload } = action;

  switch (type) {
    case 'SET_CONNECTED':
      return {
        ...state,
        connected: payload,
      };
    case 'SET_MESSAGE':
      return {
        ...state,
        messages: [payload, ...state.messages],
      };
    case 'SET_MESSAGES':
      return {
        ...state,
        messages: payload,
      };
    default:
      return state;
  }
}
