import {
  redirect,
  useLoaderData,
  useNavigate,
  useParams,
} from 'react-router-dom';
import Card from './card';
import outofstock from '../assets/imgs/out-of-stock.png';
import { useContext, useEffect, useState } from 'react';
import { AuthContext } from '../contexts/user/auth-state';

type Category = 'all' | 'pocin' | 'artefacto' | 'instrumento' | 'pergamino';

const Home = () => {
  const navigate = useNavigate();
  const { category } = useParams();

  const { user, setErrorMessage, logout } = useContext(AuthContext);
  const [products, setProducts] = useState<any>([]);
  const [info, setInfo] = useState<any>({});
  const [loading, setLoading] = useState<boolean>(true);
  console.log(user);
  useEffect(() => {
    if (!user) return;
    if (!loading) setLoading(true);
    const getProducts = async () => {
      try {
        const res = await fetch(`/api/v1/products/?category=${category}`, {
          method: 'GET',
          headers: {
            Authorization: `Bearer ${user.token}`,
          },
        });
        if (res.ok) {
          const data = await res.json();
          setProducts(data.products);
          if (!Object.keys(info).length) {
            setInfo({ ...data.info, todos: data.products.length });
          }
        } else {
          if (res.status === 401) {
            await logout();
            await setErrorMessage('Session expired');
            navigate('/auth/login', { replace: true });
          }
          const data = await res.json();
          throw new Error(data);
        }
        setLoading(false);
      } catch (err) {
        console.log(err);
      }
    };
    getProducts();
  }, [category]);

  // const products = useLoaderData() as [{ [key: string]: any }];

  return (
    <div>
      <div>
        <h3 className="text-xl font-semibold text-slate-700 text-center">
          Categorias
        </h3>
        <button
          onClick={() => {
            navigate('/');
          }}
          className={`m-1.5 py-1 px-2 ${
            !category
              ? 'bg-slate-50 shadow-md'
              : 'bg-slate-50/50  hover:shadow hover:bg-slate-50'
          } rounded-md border border-slate-200 trnasition-all`}
        >
          <p>Todos ({info['todos']})</p>
        </button>
        <button
          onClick={() => {
            navigate('/artefactos');
          }}
          className={`m-1.5 py-1 px-2 ${
            category == 'artefactos'
              ? 'bg-slate-50 shadow-md'
              : 'bg-slate-50/50  hover:shadow hover:bg-slate-50'
          } rounded-md border border-slate-200 trnasition-all`}
        >
          <p>Artefactos ({info['artefactos']})</p>
        </button>
        <button
          onClick={() => {
            navigate('/pociones');
          }}
          className={`m-1.5 py-1 px-2 ${
            category == 'pociones'
              ? 'bg-slate-50 shadow-md'
              : 'bg-slate-50/50  hover:shadow hover:bg-slate-50'
          } rounded-md border border-slate-200 trnasition-all`}
        >
          <p>Pociones ({info['pociones']})</p>
        </button>
        <button
          onClick={() => {
            navigate('/instrumentos');
          }}
          className={`m-1.5 py-1 px-2 ${
            category == 'instrumentos'
              ? 'bg-slate-50 shadow-md'
              : 'bg-slate-50/50  hover:shadow hover:bg-slate-50'
          } rounded-md border border-slate-200 trnasition-all`}
        >
          <p>Instrumentos ({info['instrumentos']})</p>
        </button>
        <button
          onClick={() => {
            navigate('/pergaminos');
          }}
          className={`m-1.5 py-1 px-2 ${
            category == 'pergaminos'
              ? 'bg-slate-50 shadow-md'
              : 'bg-slate-50/50  hover:shadow hover:bg-slate-50'
          } rounded-md border border-slate-200 trnasition-all`}
        >
          <p>Pergaminos ({info['pergaminos']})</p>
        </button>
      </div>
      <div className="w-[70%] h-0.5 mx-[15%] bg-[#5d712d80]" />
      {loading ? (
        <div className="top-0 left-0 w-full h-full flex justify-center items-center">
          <div className="animate-spin rounded-full h-28 w-28 border-t-[.25rem] border-b-[.25rem] border-[#5d712d]"></div>
        </div>
      ) : !products.length ? (
        <p className="text-xl font-semibold text-slate-700 text-center">
          No hay productos disponibles
        </p>
      ) : (
        products &&
        products.map((product: any) => (
          <Card key={product._id} product={product} />
        ))
      )}
    </div>
  );
};

export default Home;
